.button {
    background-color: white;
    border: 2px solid black;
    color: black;
    padding: 2px 2px;
    border-radius: 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    transition-duration: 0.4s;
    width: 150px;
    margin: 2px;
}

.button:hover {
    background-color: #e7e7e7;
    color: black;
  }